html {
    box-sizing: border-box;
    font-size: 100%;
}

*, *:before, *:after {
    box-sizing: inherit;   
}

* {
    padding: 0;
    margin: 0;
    text-decoration: none;
}


// helper utilites classess

.wrapper-inner {
    max-width: 1170px;
    margin: 0 auto;
}

.w-100 {    
    width: 100%;
}

.w-50 {    
    width: 50%;
}

.w-50 {
    width: 50%;
}

.pb-0 {
    padding-bottom: 0 !important;
}

.pb-1 {
    padding-bottom: 1em !important;
}

.pb-5 {
    padding-bottom: 5em !important;
}
.pb-6 {
    padding-bottom: 6em;
}

img {
    max-width: 100%;
    height: auto;
}

