.heading-h3 {
    font-family: 'futurastd-light', sans-serif;
    color: $lBlue;
    padding-bottom: 1em;
    text-transform: capitalize;
    font-size: 1.2em;
}

.heading-h2-white {
    color: $bgColor !important;
}

.rounded {
    border-radius: 100%;
    width: 180px;
    height: 180px;
}

////////////////
// mobile
@include breakpoint(mobile) {

    // .heading-h2-white {
    //     color: $bgColor;
    // }

    .btn {
        position: relative;
        z-index: 5;
        font-family: futurastd-light, sans-serif;
        color: #fff;
        text-transform: uppercase;
        border: 2px solid $lBlue;
        letter-spacing: 1px;
        background-color: transparent;
        padding: 1.2em 5.6em;
        font-size: .8rem;
        cursor: pointer;
        overflow: hidden;
    }
    
    .heading-h2 {
        position: relative;
        z-index: 5;
        font-family: 'futura-condensedbold';
        color: $h1-color;
        text-transform: uppercase;
        font-size: 1.5rem;
        text-align: center;
    }

}

@include breakpoint(large-desktop) {
    .btn {
        position: relative;
        z-index: 5;
        font-family: futurastd-light,sans-serif;
        color: #fff;
        text-transform: uppercase;
        border: 2px solid $lBlue;
        letter-spacing: 1px;
        background-color: transparent;
        padding: 1.2em 5.6em;
        font-size: .8rem;
        cursor: pointer;
        overflow: hidden;
    }

    .heading-h2 {
        position: relative;
        z-index: 5;
        font-family: 'futura-condensedbold';
        color: $h1-color;
        text-transform: uppercase;
        font-size: 2.9rem;
        text-align: start;
    }


}